* {
  margin: 0;
  padding: 0;
}

img {
  width: 20px;
  height: 20px;
}

a {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #212121;
  margin: 0 auto;
  height: 70px;
  width: 100%;

}

.title {
  color: white;

}

.title_container{
  display: flex;
  align-items:center;
  justify-content: space-between;
  padding: 0 10px;
  
  width: 170px;
}
/*Try justify-self: center or smth*/
.navbar {
  display: flex;
  justify-self: center;
  background-color: #212121;
  border: 1px solid white;
  border-radius: 12px;
  overflow: hidden;
  padding: 5px 5px;
}

.navbar a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 10px 16px;
  text-decoration: none;
  font-size: 17px;
  width: 95px;
}

.navbar a:hover {
  background-color: #ddd;
  color: black;
}

.navbar a.active {
  background-color: #949494;
  color: white;
}

.menu {
  padding: 0 10px;
}

