@media screen and (max-width:768px) {
  .navbar p {
    display: none;
    padding: 0px 0px;
    width: 0px;
    height: 0px;
  }

  .navbar a {
    padding: 4px 4px;
    width: 30px;;

  }

  body{
    background-color: blue;
  }
  
}

